/* All photos from pixabay.com */
/*  You can use any resolution of photos for gallery - gallery is full responive */
body {
  width: 100%;
  margin: 0;
  padding: 0; }

#close, #forward, #back, #showed_image {
  /*Disable select */
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                  supported by Chrome and Opera */ }

/*style for gallery iamge */
#gallery_img img {
  cursor: pointer; }

/*showiamge - dark oppacity background */
#show_image {
  position: fixed;
  left: 0;
  top: 0;
  /*its parent*/
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 110;
  display: block;
  width: 100%;
  height: 100%; }

#close {
  cursor: pointer;
  text-align: center;
  font-size: 25px;
  position: absolute;
  /*stick to top right corner its parent*/
  top: 0px;
  right: 0px;
  width: 40px;
  height: 40px;
  background-color: #555555;
  border: solid 1px;
  z-index: 110;
  opacity: 0.9;
  display: block; }

#forward {
  display: block;
  font-wight: 700;
  cursor: pointer;
  height: 1.3em;
  width: 1em;
  font-size: 3em;
  position: absolute;
  top: 45%;
  right: 0px;
  background-color: #555555;
  border: solid 1px;
  z-index: 110;
  opacity: 0.6; }
  #forward p {
    text-align: center;
    vertical-align: middle;
    line-height: 1.1em; }

#back {
  display: block;
  font-wight: 700;
  cursor: pointer;
  height: 1.3em;
  width: 1em;
  font-size: 3em;
  vertical-align: middle;
  position: absolute;
  top: 45%;
  left: 0px;
  width: 50px;
  background-color: #555555;
  border: solid 1px;
  z-index: 110;
  opacity: 0.6; }
  #back p {
    text-align: center;
    vertical-align: middle;
    line-height: 1.1em; }

#close:hover {
  background-color: #777777; }

#forward:hover {
  background-color: #777777;
  opacity: 0.9; }

#back:hover {
  background-color: #777777;
  opacity: 0.9; }

#showed_image {
  height: auto;
  width: auto;
  padding: 0;
  border: solid 1px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

#showed_image img {
  height: auto;
  width: 100%; }

#gallery {
  text-align: center;
  margin-top: 5em;
  padding-bottom: 2em; }

#gallery_img {
  position: relative;
  overflow: hidden;
  margin-top: 2em;
  width: 100%;
  height: 80%;
  box-shadow: 10px 10px 5px grey;
  transition: transform 0.3s ease-in-out; }

#gallery img {
  height: 100%;
  width: 100%; }

#gallery_img:hover {
  transform: scale(0.95); }
